.form-page{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10vh 0;
    padding-top: 45vh;
    clip-path: polygon(0 0, 0 100%, 100% 100%, 100% 0);
}

.form-page .content-wrap{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 2.5vw;
    /* background-color: aqua; */
}

.content-wrap .welcome{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 7vw;
}

.content-wrap .welcome h1:nth-child(1){
    font-size: 6vw;
    line-height: 1.05em;
}

.content-wrap .welcome .welcome-para{
    width: 310px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    flex-direction: column;
    /* background-color: burlywood; */
}

.content-wrap .welcome .welcome-para p{
    font-size: 17px;
    letter-spacing: 0px;
    line-height: 1.3em;
}

.content-wrap form{
    padding-top: 10vh;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 2.5vw;
}

.content-wrap form .input-wrap input{
    width: 450px;
    margin-top: 5px;
    padding: 20px 0;
    border: 0px solid #000;
    border-bottom: 1px solid #00000065;
    outline: none;
}

.content-wrap form .input-wrap h2{
    text-transform: uppercase;
    font-weight: 900;
    color: #121212d5;
}

.content-wrap form button{
    border: none;
    background-color: transparent;
}

.form-page .vignette{
    position: fixed;
    right: 4%;
    top: 30%;
    transform: translate(0,0%);
    width: 22vw;
    height: 52vh;
    overflow: hidden;
}

.content-wrap .welcome .mobile-form-photo{
    display: none;
}

@media (max-width:1024px) and (min-height:1000px) {
    .form-page{
        padding-right: 10vw;
        padding-top: 30vh;
    }
    .form-page .vignette{
        height: 22vh;
        width: 25vw;
    }
    .content-wrap .welcome h1:nth-child(1){
        font-size: 6.5vw;
    }
    .content-wrap .welcome .welcome-para p{
        font-size: 15px;
    }
    .content-wrap form .input-wrap h2{
        font-size: 15px;
    }
    .content-wrap form{
        padding-top: 5vh;
        gap: 4.8vw;
    }
    .content-wrap form .input-wrap input{
        margin: 0;
        width: 350px;
        padding: 15px 0;
    }
}

@media  (max-width:576px) and (max-height:1000px) {
    .form-page .vignette{
        display: none;
    }
    .form-page{
        /* background-color: rgb(102, 93, 95); */
        padding: 10vh 0;
        padding-top: 25vh;
    }
    .form-page .content-wrap{
        width: 90%;
        /* background-color: beige; */
    }
    .content-wrap .welcome .mobile-form-photo{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
        gap: 5px;
    }
    .content-wrap .welcome .mobile-form-photo img{
        object-fit: cover;
        width: 100vw;
        height: 50vh;
        position: relative;
        left: -5vw;
    }
    .content-wrap .welcome .mobile-form-photo h2:nth-child(2){
        margin-bottom: 4vh;
    }
    .content-wrap .welcome{
        width: 100%;
        gap: 10vw;
    }
    .content-wrap .welcome .welcome-para{
        width: 100%;
        align-items: flex-start;
    }
    .content-wrap .welcome .welcome-para p:nth-child(1){
        position: relative;
        left: 10vw;
    }
    .content-wrap .welcome h1:nth-child(1){
        font-size: 12vw;
    }
    .content-wrap .welcome .welcome-para p{
        line-height: 1.35em;
    }
    .content-wrap form{
        padding-top: 5vh;
        width: 100%;
        gap: 8.5vw;
    }
    .content-wrap form .input-wrap input{
        width: 90vw;
        padding: 10px 0;
    }
}
