.team-section{
    background-color: #fff;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15vh 0;
    padding-bottom: 20vh;
    overflow: hidden;
}

.team-section .mobile-scroll-text, .team-section .mobile-mode{
    display: none;
}

.team-section .mobile-scroll{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
}


.team-section .mobile-scroll .team-section-inner{
    display: flex;
    align-self: flex-start;
    justify-content: space-between;
    width: 96%;
    /* background-color: brown; */
}

.team-section-inner .stack-members:nth-child(3){
    justify-content: flex-end;
}

@media (max-width:1024px) and (min-height:1000px) {
    .team-section{
        padding: 10vh 0;
        padding-bottom: 5vh;
    }
}

@media (max-width:576px) and (max-height:1000px) {
    .team-section{
        pointer-events: all;
    }
    .team-section .mobile-scroll-text{
        display: flex;
        align-self: flex-start;
        position: relative;
        left: 5vw;
        bottom: 4vw;
    }
    .team-section .mobile-scroll-text h1{
        font-size: 8vw;
        /* text-transform: capitalize; */
    }
    .team-section{
        padding: 10vh 0;
        padding-bottom: 1vh;
        gap: 3vw;
        flex-direction: column;
    }
    .team-section .mobile-scroll{
        width: 90%;
        justify-content: flex-start;
        pointer-events: all;
        /* background-color: blue; */
    }
    .team-section .mobile-mode{
        display: block;
        position: relative;
        left: 5vw;
        align-self: flex-start;
    }
    .team-section .mobile-scroll .team-section-inner{
        width: fit-content;
        justify-content: flex-start;
        align-items: center;
        gap: 2.5vw;
        flex-shrink: 0;
    }
}

