.frame-wrap{
    width: 30%;
    height: 80vh;
    background-color: black;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 40px;
}

.frame-wrap .team-poster{
    /* position: relative; */
    background-color: #121212;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 1;
}

.frame-wrap .team-poster h1{
    color: #fff;
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.frame-wrap .team-poster h1 span{
    color: #fff;
}

.frame-wrap .member-frame{
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    transform-origin: 50% 100%;
    opacity: 0;
    pointer-events: none;
}

.member-frame .member-frame-inner{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    padding: 25px 20px;
}

.member-frame-inner img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
    position: absolute;
    left: 0;
    top: 0;
}

.member-frame-inner .content{
    position: relative;
    pointer-events: all;
}

.member-frame-inner .content h2{
    color: #fff;
    margin-bottom: 5px;
    letter-spacing: 0.7px;
    font-family: a1;
    font-weight: 100;
    position: relative;
    opacity: 1;
}

.member-frame-inner .content .role{
    color: #ffffffc4;
}

.frame-wrap .team-mode{
    position: absolute;
    left: 50%;
    top: 102%;
    transform: translate(-50%,0%);
    pointer-events: all;
}

@media (max-width:1024px) and (min-height:1000px) {
    .frame-wrap{
        height: 33vh;
        width: 35%;
    }
    .frame-wrap .team-mode{
        top: 105%;
        width: 200px;
        display: flex;
        justify-content: center;
    }
    .frame-wrap .team-mode .content h2{
        font-size: 15px;
    }
    .member-frame .member-frame-inner{
        padding: 20px 20px;
    }
}

@media (max-width:576px) and (max-height:1000px) {
    .frame-wrap{
        display: none;
    }
}

