.member{
    width: 9.5vw;
    height: 25vh;
    /* background-color: #121212; */
    cursor: pointer;
    position: relative;
    pointer-events: all;
    user-select: none;
}

.member .mobile-member-content{
    display: none;
}

.member img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: all;
}

@media (max-width:1024px) and (min-height:1000px) {
    .member{
        width: 8.5vw;
        height: 8vh;
    }
}

@media (max-width:576px) and (max-height:1000px) {
    .member{
        width: 180px;
        height: 260px;
        flex-shrink: 0;
    }
    .member .mobile-member-content{
        display: block;
        position: absolute;
        left: 15px;
        bottom: 10px;
    }
    .member .mobile-member-content h2{
        color: #fff;
        margin-bottom: 7px;
        letter-spacing: 0.7px;
        font-family: a1;
        font-weight: 900;
        font-size: 14px;
        position: relative;
    }
    .member .mobile-member-content .role{
        color: #ffffffbd;
    }
}


