.contactPage{
    width: 100%;
}

.faltu{
    margin-top: 10vh;
}

.locations{
    height: 100vh;
    width: 100%;
    padding-right: calc(250px / 2);
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #121212;
    margin-bottom: 30vh;
    clip-path: polygon(0 0, 0% 100%, 100% 100%, 100% 0);
}

.locations .location-content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 0.5vw;
}

.location-content .location-text-block{
    margin-bottom: 2vw;
}

.locations .location-content h1{
    font-size: 6vw;
    color: #fff;
}

.locations .vignette{
    position: fixed;
    right: 4%;
    top: 30%;
    transform: translate(0,0);
    width: 22vw;
    height: 52vh;
    overflow: hidden;
}

.locations .vignette .vignette-inner{
    width: 100%;
    height: 100%;
    position: relative;
}

.location-content .location-heading{
    display: flex;
    align-items: flex-end;
    justify-content: center;
    gap: 5px;
}

.location-heading .arrow{
    width: 20px;
    height: 20px;
    margin-bottom: 15px;
    transform-origin: 0% 100%;
    pointer-events: none;
}

.location-heading .arrow img::selection{
    background-color: transparent;
}

.location-content .location-item-mobile{
    display: none;
}

.fixed-contact{
    position: fixed;
    left: 2%;
    bottom: 5vh;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 7px;
}

.fixed-contact a{
    font-family: a2;
    letter-spacing: 0.2px;
    font-size: 17px;
    text-decoration: none;
}

.form-fixed-contact a{
    color: #121212;
}

.location-fixed-contact a{
    color: #fff;
    font-weight: 100;
    font-family: a1;
    letter-spacing: 0.7px;
}

.fixed-location-address{
    position: fixed;
    bottom: 18vh;
    left: 2%;
    display: flex;
    align-self: flex-start;
    flex-direction: column;
    gap: 5px;
}

.fixed-location-address h2{
    color: #ffffff;
    font-family: a1;
    letter-spacing: 0.7px;
}

@media (max-width:1024px) and (min-height:1000px) {
    .locations{
        margin-bottom: 15vh;
    }
    .locations .vignette{
        height: 22vh;
        width: 25vw;
    }
    .faltu{
        margin-top: 0vh;
    }
    .fixed-contact{
        bottom: 2vh;
    }
    .fixed-location-address{
        bottom: 10vh;
    }
    .fixed-contact a{
        font-size: 14px;
    }
    .fixed-location-address h2{
        font-size: 14px;
    }
}

@media  (max-width:576px) and (max-height:1000px) {
    .faltu{
        margin-top: 3vh;
    }
    .locations{
        padding: 10vh 0;
        margin-bottom: 0;
        height: fit-content;
    }
    .locations .vignette{
        display: none;
    }
    .location-content .location-heading{
        display: none;
    }
    .locations .location-content{
        width: 90%;
        gap: 10vw;
        /* background-color: blue; */
    }
    .location-content .location-item-mobile{
        display: flex;
        align-items: flex-start;
        justify-content: center;
        flex-direction: column;
        /* margin: 5vh 0; */
        gap: 4vw;
    }
    .location-content .location-item-mobile h1{
        color: #fff;
        font-size: 14vw;
    }
    .location-content .location-item-mobile img{
        width: 100vw;
        position: relative;
        left: -5vw;
        height: 50vh;
        object-fit: cover;
        pointer-events: none;
    }
    .location-content .location-item-mobile .address h2{
        color: #fff;
        letter-spacing: 0.2px;
        font-size: 17px;
        margin: 5px 0;
    }
    .location-content .location-item-mobile .contact{
        display: flex;
        flex-direction: column;
        gap: 5px;
    }
    .location-content .location-item-mobile .contact a{
        font-family: a2;
        letter-spacing: 0.2px;
        font-size: 17px;
        color: #fff;
        text-decoration: none;
    }
    .fixed-contact{
        display: none;
    }
    .fixed-location-address{
        display: none;
    }
}

