.stack-members{
    width: 34%;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;
    pointer-events: none;
    row-gap: 1vw;
    column-gap: 1vw;
}

@media (max-width:576px) and (max-height:1000px) {
    .stack-members{
        width: fit-content;
        align-items: center;
        justify-content: flex-start;
        flex-wrap: nowrap;
        flex-shrink: 0;
        gap: 2.5vw;
    }
}


